import React, { useEffect, useState } from 'react';


function Home(props) {
    const [lastEpisodeTitle, setLastEpisodeTitle] = useState("");
    const [newEpisodeNumber, setNewEpisodeNumber] = useState("");

    const getRSSFeed = () => {
        const waypointRSS = "https://rss.acast.com/vicegamingsnewpodcast";

        fetch(waypointRSS)
            .then(response => response.text())
            .then(xmlString => {
                const parser = new DOMParser();
                const dom = parser.parseFromString(xmlString, 'application/xml');
                return dom;
            })
            .then(dom => {
                const items = dom.querySelectorAll('item');
                const lastTitle = items[0].querySelectorAll('title')[0].textContent;
                setLastEpisodeTitle(lastTitle);
                var m = parseTitleString();
            })
    }

    const parseTitleString = () => {
        let getPart = lastEpisodeTitle.search(/Episode(\d +):/);

        const regex = /(\d+)/gm;
        const str = lastEpisodeTitle;
        let m;

        while ((m = regex.exec(str)) !== null) {
            // This is necessary to avoid infinite loops with zero-width matches
            if (m.index === regex.lastIndex) {
                regex.lastIndex++;
            }

            // The result can be accessed through the `m`-variable.
            m.forEach((match, groupIndex) => {
                let lastEpisodeNumber = parseInt(match);
                setNewEpisodeNumber(lastEpisodeNumber + 1);
            });
        }

        return newEpisodeNumber;
    }

    useEffect(() => getRSSFeed(), []);

    return (
        <div>
            <h1>The next episode of Waypoint Radio should be number...</h1>
            <p>{ newEpisodeNumber}</p>
            <p>The last episode was: {lastEpisodeTitle}</p>
        </div>

    );
}

export default Home;